import { FETCH_WEATHER } from "../actions/index";

export default function(state = [], action) {
  switch(action.type) {
    case FETCH_WEATHER:
        // return state.concat([action.payload.data]);
        return [action.payload.data, ...state] //this is ES6 new syntax doing exactly same as 
                                                //in previous line 
                                                //return [city, city, city ]
        //never manipulate the state by just calling 'this.state...'
  }
  return state;
}



